define('models/Course',[], function () {
    "use strict";

    var logo = "data:image/png;base64,";

    function Course(id, title, author) {
        this.id = id;
        this.title = title;
        this.author = author;
        this.logo = logo;
        this.objectives = [];
        this.introductionContent = '';
    };

    return Course;
});
